/* mobile styles first */
.NavItem__NavItem__r-b9L {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavItem__NavItem__r-b9L a {
    color: black;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

@media (max-width: 499px) {
    .NavItem__NavItem__r-b9L a:hover,
    .NavItem__NavItem__r-b9L a:active,
    .NavItem__NavItem__r-b9L a.NavItem__active__2ZZNc {
        color: #e87731;
    }
}


/* desktop media query */
@media (min-width: 500px) {
    .NavItem__NavItem__r-b9L {
        margin: 0;
        display: -webkit-flex;
        display: flex;
        height: 100%;
        width: auto;
        -webkit-align-items: center;
                align-items: center;
    }
    
    .NavItem__NavItem__r-b9L a {
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavItem__NavItem__r-b9L a:hover {
        border-bottom: 4px solid #FFEF00;
        color: #FFEF00;
    }
    .NavItem__NavItem__r-b9L a:active,
    .NavItem__NavItem__r-b9L a:active {
        border-bottom: 4px solid blue;
        color: blue;
    }
}